.formulario {
    p {
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend{
        font-size: 2rem;
        color: $grisOscuro;
    }

    label {
        font-weight: bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        border-bottom: 2rem;
    }

    textarea {
        height: 20rem;
        resize: none;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input [type="radio"]{
        margin: 0;
        width: auto;
    }
    input [type="submit"]{
        padding-top: 4rem;
    }
    .boton-verde {
        margin-top: 2rem;
    }
}


.contenedor {
    .formulario-login {
        padding-top: 2rem;
        padding-bottom: 5rem;
    
        p {
            font-size: 1.4rem;
            color: $grisOscuro;
            margin: 2rem 0 0 0;
        }
    
        legend{
            font-size: 2rem;
            color: $grisOscuro;
        }
    
        label {
            font-weight: bold;
            text-transform: uppercase;
            display: block;
        }
        input:not([type="submit"]),
        textarea,
        select {
            padding: 1rem;
            display: block;
            width: 100%;
            background-color: $blanco;
            border: 1px solid $gris;
            border-radius: 1rem;
            border-bottom: 2rem;
        }
    
        textarea {
            height: 20rem;
        }
    
        .forma-contacto {
            max-width: 30rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        input [type="radio"]{
            margin: 0;
            width: auto;
        }
        input [type="submit"]{
            padding-top: 4rem;
        }
        .boton-verde {
            margin-top: 6rem;
        }
        fieldset {
            height: 27rem;
            display: grid;
            label {
                padding-top: 1rem;
            }
        }
    }
}